//== Popup variables
//
//##
/*color */
//default
@popup-bg: #fff;
@popup-border-color: #fff;
@popup-head-bg: #fff;
@popup-head-border-color: #ededed;
@popup-title-color: #323232;
@popup-hint-text-color: #555;
@popup-head-option-color: #cccccc;
//primary
@popup-primary-bg: @color-primary;
@popup-primary-border: darken(@color-primary, 5%);
@popup-primary-text: #fff;

//success
@popup-success-bg: @color-success;
@popup-success-text: #fff;
@popup-success-border: darken(@color-success, 5%);

//info
@popup-info-bg: @color-info;
@popup-info-text: #fff;
@popup-info-border: darken(@color-info, 5%);

//warning
@popup-warning-bg: @color-warning;
@popup-warning-text: #fff;
@popup-warning-border: darken(@color-warning, 5%);

//danger
@popup-danger-bg: fade(@color-danger, 70%);
@popup-danger-text: #fff;
@popup-danger-border: darken(@color-danger, 5%);


// light color
@popup-light-primary-bg: tint(@color-primary, 75%);
@popup-light-primary-text: darken(@color-primary, 7%);
@popup-light-primary-border: shade(@color-primary, 5%);

@popup-light-success-bg: tint(@color-success, 75%);
@popup-light-success-text: darken(@color-success, 7%);
@popup-light-success-border: shade(@color-success, 5%);

@popup-light-info-bg: tint(@color-info, 75%);
@popup-light-info-text: darken(@color-info, 7%);
@popup-light-info-border: shade(@color-info, 5%);

@popup-light-warning-bg: tint(@color-warning, 75%);
@popup-light-warning-text: darken(@color-warning, 7%);
@popup-light-warning-border: shade(@color-warning, 5%);

@popup-light-danger-bg: tint(@color-danger, 75%);
@popup-light-danger-text: darken(@color-danger, 7%);
@popup-light-danger-border: shade(@color-danger, 5%);

@popup-border-width: 0;
@popup-radius: 10px;
@popup-head-height: 50px;
@popup-head-border-width: 1px;
@popup-title-font-size: 16px;
@popup-head-option-font-size: 18px;
@popup-hint-text-font-size: 14px;

.popupSkin(@bgColor;
@borderColor;
@headbg;
@headBorder;
@titleColor;
@optionColor;
@hintColor) {
    background-color: @bgColor;
    border: @borderColor;

    .popup-head {
        border-color: @headBorder;
        background-color: @headbg;
    }

    .popup-title {
        color: @titleColor;
    }

    .popup-option {
        .evicon {
            color: @optionColor;

            &:hover {
                color: darken(@optionColor, 10%);
            }
        }
    }

    .popup-hint-info {
        color: @hintColor;
    }
}

.popup-shade {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #000;
    .opacity(.1);
}

.popup-move-shade {
    position: absolute;

    >div {
        width: 100%;
        height: 100%;
        display: inline-block;
        background-color: #fff;
        border: 1px solid #aaa;
        .opacity(.2);
    }
}

.popup {
    .animation-duration(.2s);
    overflow: hidden;
    position: fixed;
    border-radius: @popup-radius;
    border-style: solid;
    border-width: @popup-border-width;
    .box-shadow(2px 2px 3px rgba(0, 0, 0, .4));

    .popup-loading-wait {
        position: absolute;
        top: @popup-head-height + @popup-head-border-width;
        right: 0;
        bottom: 0;
        left: 0;
        background: @popup-bg url(data:image/gif;base64,) center center no-repeat;
    }

    .popup-head {
        height: @popup-head-height;
        border-style: solid;
        border-width: 0 0 @popup-head-border-width 0;
        line-height: @popup-head-height;
    }

    .popup-title {
        display: inline-block;
        font-size: @popup-title-font-size;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding-left: 15px;
    }

    .popup-option {
        position: absolute;
        right: 10px;
        top: 0;
        text-align: right;
        line-height: @popup-head-height;
        font-size: 0;
        min-width: 90px;

        +.popup-head {
            .popup-title {
                margin-right: 90px;
            }
        }

        .evicon {
            vertical-align: middle;
            font-size: @popup-head-option-font-size;
            margin-right: .4em;
            cursor: pointer;
            line-height: inherit;

            &:hover {
                color: darken(@popup-head-option-color, 10%);
            }

            &[data-action="orig"] {
                display: none;
            }
        }
    }

    .popup-hint-info {
        padding: 20px;
        min-width: 110px;

        .evicon {
            font-size: 30px;
            margin-right: 10px;
            margin-top: -.4em;
            float: left;

            &.load-wait-1 {
                margin-top: -.2em;
                background: url('data:image/gif;base64,') center center no-repeat;
                width: 32px;
                height: 32px;
            }
        }

        .hint-text {
            font-size: @popup-hint-text-font-size;
            display: block;
            overflow: hidden;
        }
    }

    .popup-iframe-con {
        min-width: 500px;
    }

    .popup-but-area {
        text-align: center;

        .popup-but {
            display: inline-block;
            padding: 10px 10px 15px;
        }

        .btn {
            margin-left: 10px;

            &:first-child {
                margin-left: 0;
            }
        }
    }

    //
    &.popup-loading {
        background-color: transparent;
        .box-shadow(none);

        .loading-type-1 {
            width: 125px;
            height: 125px;
            background: url('data:image/gif;base64,') center center no-repeat;
        }

        .loading-type-2 {
            background: url('data:image/gif;base64,') center center no-repeat;
            width: 32px;
            height: 32px;
        }
    }

    &.popup-alert {
        &.wrap-popup {
            .popup-head {
                text-align: center;
                border-bottom: 0;

                .popup-title {
                    margin: 0;
                    padding: 0;
                }

                +.popup-body {
                    .popup-hint-info {
                        padding-top: 0;
                    }
                }
            }

            .popup-hint-info {
                text-align: center;
            }

            .popup-but {
                border-top: 1px solid @gray-light;
                display: flex;
                flex-wrap: nowrap;
                justify-content: center;
                padding: 0;

                .btn {
                    flex: 1;
                    background-color: transparent;
                    height: 100%;
                    padding: 0;
                    border-width: 0 1px 0 0;
                    border-style: solid;
                    border-color: @gray-light;
                    line-height: 0.8rem;
                    font-size: 0.28rem;

                    &:hover,
                    &:focus {
                        color: #222;
                    }

                    &:last-child {
                        border-right: 0;
                    }
                }
            }
        }
    }

    &.popup-size-min {
        width: 200px;
        height: 50px;

        .popup-title {
            width: 100px;
        }

        .popup-option {
            .evicon {

                &[data-action="min"],
                &[data-action="max"] {
                    display: none;
                }

                &[data-action="orig"] {
                    display: inline-block;
                }
            }
        }
    }

    &.popup-size-max {
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;

        .popup-option {
            .evicon[data-action="max"] {
                display: none;
            }

            .evicon[data-action="orig"] {
                display: inline-block;
            }
        }
    }

    & {
        .popupSkin(@popup-bg;
        @popup-border-color;
        @popup-head-bg;
        @popup-head-border-color;
        @popup-title-color;
        @popup-head-option-color;
        @popup-hint-text-color)
    }

    &.popup-point {
        margin: 30px;
        .box-shadow(none);

        &.primary {
            .popupSkin(@popup-primary-bg;
            @popup-primary-border;
            @popup-primary-bg;
            @popup-primary-text;
            @popup-primary-text;
            @popup-primary-text;
            @popup-primary-text);

            .evicon {
                color: @popup-primary-text;
            }
        }

        &.success {
            .popupSkin(@popup-success-bg;
            @popup-success-border;
            @popup-success-bg;
            @popup-success-text;
            @popup-success-text;
            @popup-success-text;
            @popup-success-text);

            .evicon {
                color: @popup-success-text;
            }
        }

        &.info {
            .popupSkin(@popup-info-bg;
            @popup-info-border;
            @popup-info-bg;
            @popup-info-text;
            @popup-info-text;
            @popup-info-text;
            @popup-info-text);

            .evicon {
                color: @popup-info-text;
            }
        }

        &.warning {
            .popupSkin(@popup-warning-bg;
            @popup-warning-border;
            @popup-warning-bg;
            @popup-warning-text;
            @popup-warning-text;
            @popup-warning-text;
            @popup-warning-text);

            .evicon {
                color: @popup-warning-text;
            }
        }

        &.danger {
            .popupSkin(@popup-danger-bg;
            @popup-danger-border;
            @popup-danger-bg;
            @popup-danger-text;
            @popup-danger-text;
            @popup-danger-text;
            @popup-danger-text);

            .evicon {
                color: @popup-danger-bg;
            }
        }

        &.danger-opacity {
            .popupSkin(@popup-danger-bg;
            @popup-danger-border;
            @popup-danger-bg;
            @popup-danger-text;
            @popup-danger-text;
            @popup-danger-text;
            @popup-danger-text);

            .evicon {
                color: @popup-danger-bg;
            }
        }

        &.light-primary {
            .popupSkin(@popup-light-primary-bg;
            @popup-light-primary-border;
            @popup-light-primary-bg;
            @popup-light-primary-text;
            @popup-light-primary-text;
            @popup-light-primary-text;
            @popup-light-primary-text);

            .evicon {
                color: @popup-light-primary-text;
            }
        }

        &.light-success {
            .popupSkin(@popup-light-success-bg;
            @popup-light-success-border;
            @popup-light-success-bg;
            @popup-light-success-text;
            @popup-light-success-text;
            @popup-light-success-text;
            @popup-light-success-text);

            .evicon {
                color: @popup-light-success-text;
            }
        }

        &.light-info {
            .popupSkin(@popup-light-info-bg;
            @popup-light-info-border;
            @popup-light-info-bg;
            @popup-light-info-text;
            @popup-light-info-text;
            @popup-light-info-text;
            @popup-light-info-text);

            .evicon {
                color: @popup-light-info-text;
            }
        }

        &.light-warning {
            .popupSkin(@popup-light-warning-bg;
            @popup-light-warning-border;
            @popup-light-warning-bg;
            @popup-light-warning-text;
            @popup-light-warning-text;
            @popup-light-warning-text;
            @popup-light-warning-text);

            .evicon {
                color: @popup-light-warning-text;
            }
        }

        &.light-danger {
            .popupSkin(@popup-light-danger-bg;
            @popup-light-danger-border;
            @popup-light-danger-bg;
            @popup-light-danger-text;
            @popup-light-danger-text;
            @popup-light-danger-text;
            @popup-light-danger-text);

            .evicon {
                color: @popup-light-danger-text;
            }
        }

        &.block {
            left: 0;
            top: 0;
            right: 0;
            margin: 0;
            border-radius: 0;

            .popup-hint-info {
                padding: 5px 10px;

                .evicon {
                    margin-top: -.2em;
                    font-size: 1.5em;
                }
            }
        }
    }
}
